.video-large-list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
  height: 250px;
  padding: 0;
  margin: 0;
  list-style: none;
  li:nth-child(1) {
    margin: 0 0 0 0;
  }
  .video-large-none {
    display: none;
  }
}
